import React, { useState, useEffect } from "react";
import db from "../firebase/config";
import FileDownloadOutlinedIcon from "@mui/icons-material/FileDownloadOutlined";
import ReactTooltip from "react-tooltip";
import Resume from "../styles/resume/nurbekRasulov.pdf";

const AboutMain = () => {
  const [aboutInfos, setAboutInfos] = useState([]);

  useEffect(() => {
    db.collection("about").onSnapshot((snapshot) =>
      setAboutInfos(snapshot.docs.map((doc) => doc.data()))
    );
  }, []);

  return (
    <div className="aboutMain">
      {aboutInfos.map((about) => (
        <div className="container">
          <img
            src={about.myImage}
            alt="My Image"
            className="myImage"
            // width={'100%'}
            // height={'60%'}
          />
          <span>
            <p className="aboutmeText">
              {about.text1}
              <div>
                <b>{about.text2}</b> {/*bold text */}
              </div>
              {about.text3}
            </p>
              <a className="resumebtn" href={Resume} download="nurbekRasulov.pdf">
                    <ReactTooltip textColor="yellow" />
                    <FileDownloadOutlinedIcon
                      className="resumeColor"
                      data-tip="Resume"
                    />Resume  Download!
                  </a>
          </span>
        </div>
      ))}
    </div>
  );
};

export default AboutMain;
