import React from 'react'

const NotFound = () =>{

return(
  <div className='errPage'>
      <div className='row'>
        <h3 className='error'>404</h3>
          <div className='col'>
            <h3>404 page not found</h3>
            <p className='errtext'> This happens when an address is entered incorrectly by the customer</p>
          </div>
              <a className='homePage' href='/' ><h3>Home Page</h3></a>
      </div>
  </div>
)}
export default NotFound;